body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.nav-wrapper {
    margin-top: 10px;
    background-color: blue;
    height: 100px;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.navbar-h1 {
    color: yellow;
    font-size: 75px;
}

.jumbotron-div {
    width: 100%;
    height: 60px;
    background-color: yellow;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

h3 {
    display: block;
}
.score-div {
    width: 100%;
    height: 30px;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.score-h3 {
    padding: 2px 20px;
}
.card-div {
    width: 225px;
    height: 225px;
    background-color: red;
    border: 5px solid yellow;
    margin: 5px;
}

img {
    width: 225px;
    height: 225px;
}

.card-div:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
.wrapper {
    padding-top: 50px;
    background: #ECEFF1;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    padding: 20px;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    overflow: auto;
  }
  
.modal-div {
    position: fixed;
    z-index: 5;
    background-color: red;
    width: 50%;
    border: 10px solid blue;
    box-shadow: 10px 10px 10px black;
    padding: 16px;
    left: 25%;
    box-sizing: border-box;
    text-align: center;
}

button {
    color: red !important;
    display: block;
    text-transform: uppercase;
    background: blue;
    padding: 20px;
    border: 4px solid yellow !important;
    border-radius: 6px;
    display: inline-block;
}

button:hover {
    color: red !important;
    background: yellow;
    border-radius: 50px;
    border-color: blue !important;
    transition: all 0.3s ease 0s;
}

image {
    display: block;
}

.btn-div {
    margin-top: 10px;
}
